
.bm-burger-button {
    position: fixed;
    width: 40px;
    height: 36px;
    left: 32px;
    top: 32px;
}

/* Color to our burger */
.bm-burger-bars {
    background: white;
}

.bm-burger-bars-hover {
    background: #FF1493;
  }

  .bm-cross-button {
    height: 35px;
    width: 35px;
  }
  .bm-cross-hover {
    background: #FF1493;
  }
  .bm-cross {
    background: #fff;
}

.bm-menu-wrap {
    position: fixed;
    /* height: 100vh; */

    height: 10%;
    overflow-x: hidden;
    box-shadow: 0px 4px 31px #ff149171;
  }

  /* General sidebar styles */
.bm-menu {
    background: black;

    padding: 0.6em 0.5em 0.5em;
    font-size: 1em;
    overflow: hidden !important;
  }

  .bm-item-list {
    color: #FF1493;
    padding: 0.8em;
  }
  .bm-item {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    /* margin-bottom: 10px; */
  }
  .menu-item {
    position: relative;
    font-size: 1.3em;
    text-decoration: none;
    color: #fff;
      font-family: 'Abril Fatface', cursive;
    font-style: normal;
    font-weight: normal;
    padding: 20px;
}
.menu-item:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    bottom: 0;
    left: 0;
    background-color: #FF1493;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
}
  .active-item {
    color:#FF1493;
}
.menu-item:hover:before,
.active-item:before {
    visibility: visible;
    height: 3px;
}

.Image{
    width: 200px;
    border-radius: 50%;
    margin-bottom: 25%;
}
